.footer {
  background: #333333;
  padding: 80px 0 25px;
}

.footer .about {
  margin-bottom: 30px;
  color: #999999;
  font-size: 12px;
  line-height: 17px;
}

.footer .adress {
  margin-bottom: 12px;
  color: #999999;
}

.footer ul {
  padding: 0;
  margin: 0;
}

.footer li {
  line-height: 32px;
  margin-bottom: 0;
}

.footer li::before {
  display: none;
}

.footer li a {
  color: #999999;
  border-bottom-color: transparent;
}

.footer li a:hover {
  color: #ED4545;
  border-bottom-color: #ED4545;
}

.footer__top {
  margin-bottom: 60px;
}

.footer__logo {
  margin-bottom: 42px;
}

.footer__logo svg {
  width: 226px;
}

.footer__logo path {
  fill: #fff;
}

.footer__phone {
  margin-bottom: 20px;
  line-height: 0.6;
}

.footer__phone a {
  color: #fff;
  border-bottom: none;
  font-style: normal;
  font-weight: 700;
  font-size: 26px;
  line-height: 28px;
}

.footer__mail {
  margin-bottom: 16px;
}

.footer__mail a {
  color: #fff;
  font-size: 15px;
}

.footer__contact__info__full a {
  color: #fff;
  font-size: 15px;
}

.footer__adress {
  padding-bottom: 18px;
}

.footer__adress a {
  border-bottom: none;
}

.footer__links__title {
  color: #fff;
  font-size: 20px;
}

.footer__links__title h4 {
  margin-top: 0;
}

.footer__links__client {
  order: 1;
}

.footer__adress-label {
  font-weight: bold;
}

.footer__bottom {
  padding-top: 25px;
  border-top: 2px solid #484848;
  padding-bottom: 14px;
  flex-flow: row wrap;
  display: flex;
  color: #999999;
  justify-content: space-between;
}

.footer__bottom a {
  color: #fff;
  text-decoration: none;
  border-bottom: 0;
}

.footer__bottom > * {
  flex: 0 1 auto;
}

.copyright {
  color: #fff;
}

@media only screen and (min-width: 768px) {
  .footer__top {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    grid-gap: 50px;
    margin-bottom: 125px;
  }

  .footer__intro {
    display: block;
  }

  .footer__adress {
    order: 2;
  }

  .footer__links__catalog {
    display: none;
  }
}

@media only screen and (min-width: 1366px) {
  .footer__top {
    grid-template-columns: repeat(auto-fit, minmax(226px, auto));
  }

  .footer__links__catalog {
    display: block;
  }
}